@import url('https://fonts.googleapis.com/css?family=Oswald');
@import url('https://fonts.googleapis.com/css?family=Raleway');

.banner-container {
	left: 0;
	right: 0;

	position: relative;
	height: 300px;

	overflow: hidden;
	/*transform-style: preserve-3d;*/
}

.banner-background {
	height: 100%;
	width: 100%;

	/* Background Image */
	background-size: cover;
	background-position: center;
	background-repeat: no-repeat;
	background-attachment: fixed;

	/* Parallax Scroll */
	/*z-index: -100;
	transform: translateZ(-500px) scale(2.1);*/
}

.banner-overlay {
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;

	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;

	background: linear-gradient(
		rgba(0, 0, 0, 0.3),
		rgba(0, 0, 0, 0.6)
	);
}

.banner-text {
	display: block;
	position: relative;

	margin: 0 40px;

	color: white;
	text-align: center;
	text-shadow: 3px 2px 8px rgba(0, 0, 0, 0.5);
}

.banner-text > h1 {
	font-family: 'Oswald', sans-serif;
	font-weight: 400;
	font-size: 72px;
	margin: 0;
}

.banner-text > p {
	font-family: 'Raleway', serif;
	font-size: 28px;
	line-height: 1.4;
}

.down-arrow {
	width: 50px;

	position: absolute;
	bottom: 30px;
	display: none;
}

@keyframes down-arrow {
	from {bottom: 30px;}
	to {bottom: 20px;}
}


/* Media Queries */

@media (max-width: 600px) {
	.banner-container {
		height: 250px;
	}

	.banner-text > h1 {
		font-size: 54px;
	}

	.banner-text > p {
		font-size: 20px;
	}
}

@media (max-height: 600px) {
	.banner-container {
		height: 100%;
	}

	.down-arrow {
		display: block;

		animation-name: down-arrow;
		animation-direction: alternate;
		animation-duration: 1s;
		animation-iteration-count: infinite;
		animation-timing-function: ease-in-out;
	}
}
