@import url(https://fonts.googleapis.com/css?family=Oswald);
@import url(https://fonts.googleapis.com/css?family=Oswald);
@import url(https://fonts.googleapis.com/css?family=Raleway);
@import url(https://fonts.googleapis.com/css?family=EB+Garamond);
body {
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;

  background-color: #E0E0E0;
}

html, body {
  height: 100%;
  overflow: hidden;
}

#root {
  height: 100%;
}

#navigation-bar {
	background-color: black;
}

#main-logo-container {
	display: block;
	overflow: hidden;

	margin-left: 10px;
	margin-right: 40px;
}

#main-logo {
	font-family: 'Oswald', sans-serif;
	color: white;

	font-size: 30px;
	line-height: 1;
	text-decoration: none;

	transition: color 0.2s ease-in-out;
}

#main-logo:hover {
	color: #ddd;
}

.navigation-tabs {
	display: block;
	overflow: hidden;
}

.mobile.navigation-tabs {
	display: none;
}

#social-buttons {
	display: block;
	overflow: hidden;

	position: absolute;
	right: 0;

	margin-right: 10px;
}

#social-buttons > a {
	margin: 0 5px;
}

.social-icon {
	width: 30px;
}

/***** Media Queries *****/
@media (max-width: 540px) {
	.mobile.navigation-tabs {
		display: block;
	}

	.desktop.navigation-tabs {
		display: none;
	}
}

.banner-container {
	left: 0;
	right: 0;

	position: relative;
	height: 300px;

	overflow: hidden;
	/*transform-style: preserve-3d;*/
}

.banner-background {
	height: 100%;
	width: 100%;

	/* Background Image */
	background-size: cover;
	background-position: center;
	background-repeat: no-repeat;
	background-attachment: fixed;

	/* Parallax Scroll */
	/*z-index: -100;
	transform: translateZ(-500px) scale(2.1);*/
}

.banner-overlay {
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;

	display: -webkit-flex;

	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	-webkit-justify-content: center;
	        justify-content: center;
	-webkit-align-items: center;
	        align-items: center;

	background: linear-gradient(
		rgba(0, 0, 0, 0.3),
		rgba(0, 0, 0, 0.6)
	);
}

.banner-text {
	display: block;
	position: relative;

	margin: 0 40px;

	color: white;
	text-align: center;
	text-shadow: 3px 2px 8px rgba(0, 0, 0, 0.5);
}

.banner-text > h1 {
	font-family: 'Oswald', sans-serif;
	font-weight: 400;
	font-size: 72px;
	margin: 0;
}

.banner-text > p {
	font-family: 'Raleway', serif;
	font-size: 28px;
	line-height: 1.4;
}

.down-arrow {
	width: 50px;

	position: absolute;
	bottom: 30px;
	display: none;
}

@-webkit-keyframes down-arrow {
	from {bottom: 30px;}
	to {bottom: 20px;}
}

@keyframes down-arrow {
	from {bottom: 30px;}
	to {bottom: 20px;}
}


/* Media Queries */

@media (max-width: 600px) {
	.banner-container {
		height: 250px;
	}

	.banner-text > h1 {
		font-size: 54px;
	}

	.banner-text > p {
		font-size: 20px;
	}
}

@media (max-height: 600px) {
	.banner-container {
		height: 100%;
	}

	.down-arrow {
		display: block;

		-webkit-animation-name: down-arrow;

		        animation-name: down-arrow;
		-webkit-animation-direction: alternate;
		        animation-direction: alternate;
		-webkit-animation-duration: 1s;
		        animation-duration: 1s;
		-webkit-animation-iteration-count: infinite;
		        animation-iteration-count: infinite;
		-webkit-animation-timing-function: ease-in-out;
		        animation-timing-function: ease-in-out;
	}
}

#page-base {
  height: 100%;
}

#page-container {
  height: calc(100% - 64px);
  margin-top: 64px;

  overflow-x: hidden;

  -webkit-perspective: 500px;

          perspective: 500px;
}

#content-container {
  background: #E0E0E0;
  z-index: 1000;
  position: relative;

  padding: 40px;
}

.text-container {
  left: 0;
  right: 0;

  width: 600px;
  max-width: 85vw;
  margin: 0 auto;
}

.text-container > p {
  text-align: justify;

  margin-top: 0;
  font-family: 'EB Garamond', serif;
  font-size: 22px;
}


/* Media queries */

@media (max-width: 1000px) {
  #content-container {
    padding: 30px;
  }
}

@media (max-width: 600px) {
  #page-container {
    margin-top: 48px;
    height: calc(100% - 48px);
  }
}

@media (max-width: 540px) {
  #page-container {
    margin-top: 104px;
    height: calc(100% - 104px);
  }

  #content-container {
    padding: 15px;
    width: calc(100% - 30px);
  }

  .text-container {
    margin-top: 15px;
  }

  .text-container > p {
    text-align: left;
  }
}

@media (max-width: 540px) and (orientation: landscape) {
  #page-container {
    margin-top: 96px;
    height: calc(100% - 96px);
  }
}

#project-container {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(350px, 1fr));
  grid-gap: 15px;
}

@media (max-width: 400px) {
    #project-container {
        grid-template-columns: 1fr;
    }
}

/***** Project Cards *****/
.project-card {
        place-self: stretch stretch;

	overflow: hidden;

	display: -webkit-flex;

	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	cursor: pointer;

	transition: all 0.2s ease-in-out;
}

.project-card:hover {
	-webkit-transform: scale(1.03);
	        transform: scale(1.03);
	box-shadow: 5px 5px 10px rgba(0,0,0,0.3);
}

.project-info-container {
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	-webkit-flex-grow: 1;
	        flex-grow: 1;
}

.project-image {
	height: 200px;
}


/*** Header ***/
.project-header {
	display: -webkit-flex;
	display: flex;
	width: 100%;
}

.project-title {
	float: left;
	overflow-x: hidden;
	-webkit-flex-grow: 1;
	        flex-grow: 1;
}

.project-date {
	float: right;
	text-align: right;
	padding-top: 3px;
}

/*** Content ***/
.project-info {
	-webkit-flex-grow: 1;
	        flex-grow: 1;
}


.project-info p {
        margin-top: 10px;
}

a {
	text-decoration: none;
	color: black;
}

/*** Footer ***/
.project-footer {
	margin-top: auto;
	display: -webkit-flex;
	display: flex;
}

/* Tags */
.project-tags-container {
	/* Fill all allotted space */
	display: -webkit-flex;
	display: flex;
	-webkit-flex-grow: 1;
	        flex-grow: 1;

	/* Positioning and element wrapping*/
	white-space: nowrap;
	margin-right: 10px;
	margin-top: 3px;

	/* Clip scrollbar in .project-tags */
	overflow: hidden;
	height: 32px;
}

.project-tag-label {
	padding-top: 9px;
}

.project-tags {
	display: inline-block;
	white-space: nowrap;
	overflow-x: auto;

	height: 60px; /* Push scrollbar out of view */
}

.project-tag {
	margin-left: 6px;
}

/* Social Icons */
.project-social {
	display: inline-block;
	float: right;
	white-space: nowrap;
}

.project-social a {
	padding: 4px;
}

.project-social a img {
	width: 32px;
}


/***** Media Queries *****/
@media (max-width: 480px) {
	.project-header {
		display: inline-block;
	}
	
	.project-title {
		width: 100%;
	}

	.project-date {
		float: left;
		text-align: left;
		margin-top: 2px;
	}
}

.tag-filter {
  margin: 10px;
  display: -webkit-flex;
  display: flex;
}

.tag-filter p {
  margin: auto 15px auto 10px;
  padding: 5px 0px 5px 0px;
  font-family: Roboto;
  font-size: 16px;
}

/* Media Queries */
@media (max-width: 850px) {
  .tag-filter {
    margin: 5px;
  }
}



