@import url('https://fonts.googleapis.com/css?family=Oswald');

#navigation-bar {
	background-color: black;
}

#main-logo-container {
	display: block;
	overflow: hidden;

	margin-left: 10px;
	margin-right: 40px;
}

#main-logo {
	font-family: 'Oswald', sans-serif;
	color: white;

	font-size: 30px;
	line-height: 1;
	text-decoration: none;

	transition: color 0.2s ease-in-out;
}

#main-logo:hover {
	color: #ddd;
}

.navigation-tabs {
	display: block;
	overflow: hidden;
}

.mobile.navigation-tabs {
	display: none;
}

#social-buttons {
	display: block;
	overflow: hidden;

	position: absolute;
	right: 0;

	margin-right: 10px;
}

#social-buttons > a {
	margin: 0 5px;
}

.social-icon {
	width: 30px;
}

/***** Media Queries *****/
@media (max-width: 540px) {
	.mobile.navigation-tabs {
		display: block;
	}

	.desktop.navigation-tabs {
		display: none;
	}
}
